(function () {
    'use strict';

    // show modal from startup
    // const demoModalEl = document.getElementById('modalForm');
    // const demoModal = bootstrap.Modal.getOrCreateInstance(demoModalEl);
    // demoModal.show();

    // console.log(demoModal);

    // "validation" form
    Array.prototype.slice.call(document.querySelectorAll('.needs-validation'))
        .forEach(function (form) {
            form.addEventListener('submit', function (event) {
                if (!form.checkValidity()) {
                    event.preventDefault()
                    event.stopPropagation()
                }

                form.classList.add('was-validated')
            }, false)
        })

})()